body {
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background: black;
    color: white;
    font-family: 'Press Start 2P', sans-serif;
  }
  
  * {
    box-sizing: border-box;
  }
  
  .menu {
    padding: 20px;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, #6c6eb2 0%,#181870 64%,#000028 100%);
    border-radius: 8px;
    border: 1px solid #424542;
    
    box-shadow: 4px 4px #e7dfe7,
                -4px -4px #e7dfe7,
                4px -4px #e7dfe7,
                -4px 4px #e7dfe7,
                0 -2px #9c9a9c,
                -2px 0 #7b757b,
                0 2px #424542;
    
  }
  
  .game-wrapper {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    margin: 0;
    padding: 10px;
  }